@import 'modern-normalize';

html {
  box-sizing: border-box;
  height: 100%;
  width: 100vw;
  overflow-x: hidden;
}

body {
  height: 100%;
  position: relative;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
}

ul,
ol,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 16px;

  @media screen and (min-width: 576px) and (max-width: 767px) {
    width: 540px;
    padding: 24px;
  }

  @media screen and (min-width: 768px) and (max-width: 1200px) {
    width: 720px;
    padding: 24px;
  }

  @media screen and (min-width: 1200px) {
    width: 1140px;
    padding: 32px;
  }
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1 1 auto;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.btn {
  min-width: 120px;
  padding: 12px 0;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent;
  border: 1px solid white;
  border-radius: 5px;
  cursor: pointer;
  transition: background 500ms ease;
  @media (max-width: 420px) {
    flex-basis: calc(
      50% - 10px
    ); /* Ширина кнопок 50% з урахуванням відступів */
  }

  // .btn--active
  &--active {
    font-size: 20px;
    &:disabled {
      color: #ff6b08;
      border: 1px solid #ff6b08;
      cursor: auto;
      &:hover {
        background-color: transparent;
      }
    }
  }

  // .btn:hover
  &:hover {
    background-color: #ff6b08;
  }

  // .btn__block
  &__block {
    display: flex;
    gap: 12px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }

  // .btn--modify
  &--modify {
    color: #111111;
    border-color: #111111;
    transition: all 250ms ease;

    // .btn--modify:disabled
    &:disabled {
      background-color: #8c8c8c;
      color: #faebd7;
      border-color: #8c8c8c;
      cursor: auto;
    }

    // .btn--modify:hover, :focus
    &:hover {
      color: #ffffff;
      border-color: transparent;
    }
  }
}

.flex {
  display: flex;
  justify-content: center;
  padding: 25px;
}

.empty {
  font-weight: 500;
  font-size: 36px;
}
